<template>
  <div class="listGroup_bg col-lg-6" id="idSection">
    <div class="listGroup">
      <span></span>
    </div>
    <CustomList :type="'text'" :title="''" :placeholder="''" @customListInput="customListInputName" />
    <CustomList :type="'tel'" :title="''" :placeholder="' '" @customListInput="customListInputPhone" />
    <CustomList :type="'email'" :title="''" :placeholder="''" @customListInput="customListInputEmail" />
  </div>
</template>

<script>
import CustomList from '@/components/common/CustomList'
export default {
  name: 'contact',
  components: {
    CustomList
  },
  data(){
    return{
      mainContact: {
        name: '',
        phone: '',
        email: ''
      }
    }
  },
  computed: {
    isInputLegal(){
      return Object.values(this.mainContact).every(val => val.length > 0)
    }
  },
  watch: {
    isInputLegal(val){
      if(val) this.contact(this.mainContact)
      else this.contact(false)
    }
  },
  methods: {
    contact(val){
      this.$emit('contactVal', val)
    },
    customListInputName(val){
      this.mainContact.name = val
    },
    customListInputPhone(val){
      this.mainContact.phone = val
    },
    customListInputEmail(val){
      this.mainContact.email = val
    }
  }
}
</script>

<style scoped lang="sass">
.listGroup
  height: 40px
</style>